import * as _moment from 'moment';
import * as d3 from 'd3';
import * as _ from 'lodash';
import {AxisOption} from './axis-option';
const moment = _moment;

const DjChartType = {
    PIE_CHART: 'pieChart',
    CLOUD_CHART: 'cloudChart',
    COMPOSITE: 'composite',
    HEATMAP: 'heatmap',
    DC_CHART: 'dcChart',
    STACKED_BAR: 'stackedBar'
}

